<script>
import Card from "../components/Card";
import { mapState, mapActions } from "vuex";

export default {
	name: "home",
	components: {
		Card
	},
	data() {
		return {
			// addStrategyForm: this.$form.createForm(this, { name: "addStrategyForm" }),
			showError: false,
			cards: [],
			modal: {
				visible: false
			}
		};
	},
	computed: mapState([
		"swiper",
		"cardsInitiated",
		"isAuthenticated",
		"appData",
		"sliderIndex",
		"show",
		"customerData",
		"swiper"
	]),
	beforeMount() {
		this.$client
			.getItems("pain_points", {
				fields: "id,text,themes.themes_id.name,themes.themes_id.color",
				status: "published"
			})
			.then((res) => {
				this.cards = res.data;
			})
			.catch((err) => {
				this.$message.error(err.message);
			});
	},
	methods: {
		...mapActions([
			"initCards",
			"setIndex",
			"addStrategy",
			"setSwiper",
			"revertCards",
			"updateChosenPainPoint",
			"doneLoading"
		]),
		hasStrategies() {
			const { chosenCard } = this.customerData;

			if (chosenCard) {
				return chosenCard.strategies != null && chosenCard.strategies.length > 0;
			} else {
				return false;
			}
		},
		slideChange() {
			// console.log(this.swiper.realIndex);
			// this.setIndex(this.swiper.realIndex);
			// this.customerData.chosenCard = this.cards[this.swiper.realIndex];
		},
		openForm() {
			this.$message.info("This is a normal message");
		},
		openModal() {
			this.modal.visible = true;
		},
		closeModal() {
			this.modal.visible = false;
		},
		handleOk() {
			this.addStrategyForm.validateFields((err, values) => {
				// console.log("validating, bro", err);

				if (!err) {
					this.$client.api
						.patch(
							"/items/pain_points/" + this.customerData.chosenCard.id,
							{
								strategies: [
									{
										strategies_id: {
											status: "published",
											text: values.strategyPoint
										}
									}
								]
							},
							{
								fields:
									"text,themes.themes_id.name,likes,strategies.strategies_id.text,strategies.strategies_id.id,strategies.strategies_id.likes"
							}
						)
						.then((res) => {
							this.$message.success("Strategy added!");
							// console.log(res);
							this.addStrategy(res);
							this.closeModal();
						})
						.catch((err) => {
							this.$message.error(err.message);
						});
				}
			});
		}
	}
};
</script>