				<div class="card-content">
					<div class="card-icon">
						<fa :icon="['fas', 'quote-left']"></fa>
					</div>
					<h5 class="card-title" v-html="title"></h5>
					<a-row type="flex" v-if="painPoint" style="padding-top: 1em;">
						<template v-if="themes && themes.length > 0">
							<a-col v-for="(theme, i) in themes" :key="i" :style="{ margin: '0 0.25rem 0.25rem 0' }">
								<a-tag
									size="small"
									:style="{ margin: 0 }"
									:color="tagColor(theme.themes_id.color).backgroundColor"
								>
									<span :style="{ color: tagColor(theme.themes_id.color).color }">
										{{ theme.themes_id.name }}
									</span>
								</a-tag>
							</a-col>
						</template>

						<template v-else>
							<a-col>
								<a-tag>
									Untagged
								</a-tag>
							</a-col>
						</template>
